.Loader {
  width: 100%;
  height: 100vh;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  background-image: -webkit-radial-gradient(transparent, #240646);
  background-image: -o-radial-gradient(transparent, #240646);
  background-image: radial-gradient(transparent, #240646);
}

.Loader::after {
  content: "";
  position: absolute;
  height: 5rem;
  width: 5rem;
  border-radius: 50%;
  border: 5px solid #b4b4b4;  
  border-top: 5px solid #558df5;
  -webkit-animation: spin 1s infinite linear;
          animation: spin 1s infinite linear;
}

@-webkit-keyframes spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}


