.Toolbar {
  position: fixed;
  width: 100%;
  height: 5rem;
  top: 0;
  left: 0;
  z-index: 20;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: justify;
      justify-content: space-between;
  -ms-flex-align: center;
      align-items: center;
  padding: 1rem 1.5rem;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#010107), color-stop(#26233d), color-stop(#454356), color-stop(#26233d), to(#010107));
  background-image: -webkit-linear-gradient(#010107, #26233d, #454356, #26233d, #010107);
  background-image: -o-linear-gradient(#010107, #26233d, #454356, #26233d, #010107);
  background-image: linear-gradient(#010107, #26233d, #454356, #26233d, #010107);
  -webkit-box-shadow: 0 1rem 2rem rgba(0, 0, 0, .5);
          box-shadow: 0 1rem 2rem rgba(0, 0, 0, .5);
  color: white;
}

@media (min-width: 600px) and (min-height: 600px) {
  .Toolbar {
    height: 6rem;
  }
}


