.Toggler {
  width: 4rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  cursor: pointer;
}

.Toggler div {
  width: 100%;
  height: 5px;
  margin: 2px 0;
  background-color: white;
  border-radius: 5px;
  -webkit-transition: all .4s;
  -o-transition: all .4s;
  transition: all .4s;
}

.Open div:first-of-type{
  -webkit-transform: translateY(9px) rotate(140deg);
      -ms-transform: translateY(9px) rotate(140deg);
          transform: translateY(9px) rotate(140deg);
}

.Open div:nth-of-type(2){
  -webkit-transform: scale(0);
      -ms-transform: scale(0);
          transform: scale(0);
}

.Open div:nth-of-type(3){
  -webkit-transform: translateY(-9px) rotate(-145deg);
      -ms-transform: translateY(-9px) rotate(-145deg);
          transform: translateY(-9px) rotate(-145deg);
}

.Close:hover div:first-of-type{
  -webkit-transform: translateY(-3px);
      -ms-transform: translateY(-3px);
          transform: translateY(-3px);
}

.Close:hover div:nth-of-type(3){
  -webkit-transform: translateY(3px);
      -ms-transform: translateY(3px);
          transform: translateY(3px);
} 


