.Menu {
  position: fixed;
  top: 5rem;
  right: 0;
  z-index: 19;
  width: 220px;
  padding: 3rem;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#010107), color-stop(#26233d), to(#454356));
  background-image: -webkit-linear-gradient(#010107, #26233d, #454356);
  background-image: -o-linear-gradient(#010107, #26233d, #454356);
  background-image: linear-gradient(#010107, #26233d, #454356);
  -webkit-box-shadow: -1rem 1rem 2rem rgba(0, 0, 0, .5);
          box-shadow: -1rem 1rem 2rem rgba(0, 0, 0, .5);
  -webkit-transition: all .5s;
  -o-transition: all .5s;
  transition: all .5s;
  border-bottom-left-radius: 1rem;
}

.Open {
  -webkit-transform: translateY(0);
      -ms-transform: translateY(0);
          transform: translateY(0);
}

.Close {
  -webkit-transform: translateY(-100vh);
      -ms-transform: translateY(-100vh);
          transform: translateY(-100vh);
}


